.home-container {
  z-index: 90;
  position: relative;
  width: 100%;
  max-width: 800px;
  height: 100vh;
  margin: 0 auto;
  padding: 24px 0;

  @media (min-width: 800px) {
    // padding: 28px 20px 88px 20px;
  }

  .home-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;

    h1 {
      color: var(--text-color);
      margin-bottom: 30px;
    }

    > button {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 9px 20px;
      margin-top: 12px;
      height: 42px;
      background: var(--background-color);
      color: var(--text-color);
      font-size: 1rem;
      border: 1px solid var(--gray-color-10);

      &:hover {
        color: var(--text-white);
        background: var(--primary-color);
        border: 1px solid var(--background-color);
      }
      border-radius: 21px;
      -webkit-border-radius: 21px;
      -moz-border-radius: 21px;
      -ms-border-radius: 21px;
      -o-border-radius: 21px;
    }
  }

  .history-section {
    padding: 24px 12px;

    @media (min-width: 800px) {
      padding-bottom: 48px;
    }

    h2 {
      color: var(--text-color);
      margin-bottom: 20px;
    }

    .history-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 12px 12px 12px 24px;
      margin: 10px 0;
      background: var(--background-color);
      border: 1px solid var(--secondary-color);
      transition: all 0.3s ease;

      &:hover {
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
      }

      .history-info {
        flex: 1;

        p {
          margin: 5px 0;
          color: var(--text-color);
        }
      }

      button {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 9px 12px;
        font-size: 0.875rem;
        background-color: var(--background-color);
        border: 1px solid var(--gray-color-12);

        &:hover {
          background: var(--gray-color-13);
        }
        border-radius: 9px;
        -webkit-border-radius: 9px;
        -moz-border-radius: 9px;
        -ms-border-radius: 9px;
        -o-border-radius: 9px;
      }
      border-radius: 21px;
      -webkit-border-radius: 21px;
      -moz-border-radius: 21px;
      -ms-border-radius: 21px;
      -o-border-radius: 21px;
    }
  }
}
