.quiz-container {
  z-index: 90;
  position: relative;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 28px 12px 88px 12px;

  @media (min-width: 800px) {
    padding: 28px 20px 88px 20px;
  }

  .question-section {
    background: var(--background-color);
    padding: 20px;
    border-radius: 5px;
    margin-bottom: 20px;

    h2 {
      color: var(--text-color);
      margin-bottom: 15px;
    }

    p {
      color: var(--text-color);
      font-size: 1.1rem;
      margin-bottom: 20px;
    }

    .options {
      display: flex;
      flex-direction: column;
      gap: 10px;

      button {
        display: flex;
        align-items: center;
        justify-content: start;
        text-align: left;
        padding: 15px;
        background: var(--gray-color-13);
        color: var(--text-color);
        border: none;
        border-radius: 4px;
        cursor: pointer;

        // &:hover {
        //   background: var(--gray-color-12);
        // }

        &.selected {
          background: var(--primary-color);
          color: white;
        }
      }
    }
  }

  .button-section {
    display: flex;
    justify-content: space-between;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    gap: 10px;
    height: 80px;
    padding: 0 12px 24px 12px;
    background: var(--background-color);

    > button {
      flex: 1;
      font-size: 1rem;

      &.finish-button {
        background: var(--primary-color);
        color: white;
      }
    }
  }
}
