.timer {
  position: fixed;
  top: 20px;
  right: 20px;
  background: var(--gray-color-13);
  padding: 12px 24px;
  border-radius: 5px;
  font-size: 1.2rem;
  color: var(--text-color);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  z-index: 100;
  transition: all 0.3s ease;

  &:hover {
    background: var(--gray-color-12);
  }
}
